import { useState, useEffect } from 'react'


import Notification from './components/Notification'
import Footer from './components/Footer'
import loginService from './services/login'
import blogService from './services/blogs'
import React from 'react'


const App = () => {
  const [blogs, setBlogs] = useState([])
  const [newBlog, setNewBlog] = useState('')

  const [newTitle, setNewTitle] = useState('')
  const [newAuthor, setNewAuthor] = useState('')
  const [newUrl, setNewUrl] = useState('')


  
  const [showAll, setShowAll] = useState(true)
  const [errorMessage, setErrorMessage] = useState(null)
  const [username, setUsername] = useState('') 
  const [password, setPassword] = useState('')
  const [user, setUser] = useState(null)

  useEffect(() => {
    blogService
      .getAll()
      .then(initialBlogs => {
        setBlogs(initialBlogs)
      })
  }, [])

  useEffect(() => {
    const loggedUserJSON = window.localStorage.getItem('loggedBlogappUser')
    if (loggedUserJSON) {
      const user = JSON.parse(loggedUserJSON)
      setUser(user)
      blogService.setToken(user.token)
    }
  }, [])

  const handleLogin = async (event) => {
    event.preventDefault()
    try {
      const user = await loginService.login({
        username, password,
      })
      setUser(user)
      blogService.setToken(user.token)
      window.localStorage.setItem(
        'loggedNoteappUser', JSON.stringify(user)
      ) 
      setUsername('')
      setPassword('')
    } catch (exception) {
      setErrorMessage('Wrong username or password!')
      setTimeout(() => {
        setErrorMessage(null)
      }, 5000)
    }
  }
//KÄY LÄPI MYÖS
  const addBlog = (event) => {
    event.preventDefault()
    const blogObject = {
      title: newTitle,
      author: newAuthor,
      url: newUrl,
      id: blogs.length + 1
    }

    blogService
      .create(blogObject)
      .then(returnedBlog => {
        setBlogs(blogs.concat(returnedBlog))
        setNewBlog('')
      })
  }

  const handleBlogChange = (event) => {
    setNewBlog(event.target.value)
  }
  const handleTitleChange = (event) => {
    event.preventDefault()

    try {
    console.log(event.target.value)
    console.log(blogs.map(x => x.title))
    setNewTitle(event.target.value)
  
  } catch (exception) {
    setErrorMessage('Tyhjä! : Title')
    setTimeout(() => {
      setErrorMessage(null)
    }, 5000)
  }
  }
  const handleAuthorChange = (event) => {
    event.preventDefault()

    try {
    console.log(event.target.value)
    console.log(blogs.map(x => x.author))
    setNewAuthor(event.target.value)
  
  } catch (exception) {
    setErrorMessage('Tyhjä! : Title')
    setTimeout(() => {
      setErrorMessage(null)
    }, 5000)
  }
    
  }
  const handleUrlChange = (event) => {
    event.preventDefault()

    try {
    console.log(event.target.value)
    console.log(blogs.map(x => x.url))
    setNewUrl(event.target.value)
  
  } catch (exception) {
    setErrorMessage('Tyhjä! : Title')
    setTimeout(() => {
      setErrorMessage(null)
    }, 5000)
  }
  }

    



  const loginForm = () => (
    <form onSubmit={handleLogin}>
      <div>
          username
          <input
            type="text"
            value={username}
            name="Username"
            onChange={({ target }) => setUsername(target.value)}
          />
      </div>
      <div>
          password
          <input
            type="password"
            value={password}
            name="Password"
            onChange={({ target }) => setPassword(target.value)}
          />
      </div>
      <button type="submit">login</button>
    </form>      
  )

  //KORJAA!
  
  
  
  const RenderNumerot = () => {
    return (

    <div>
        <h1>Blogs that have been added</h1>
      
      {blogs.map(blog =>
      <li key={blog.id}>
        {blog.title + ' ' + blog.author}
        {console.log(blog.id)}
        
            
      </li>
        )}
    </div>
    )
  }
  const blogForm = () => (
  
    <form onSubmit={addBlog}>
    <div>Title: <input value={newTitle}
                onChange={handleTitleChange} /></div>
    
    <div>Author: <input value={newAuthor} 
                  onChange={handleAuthorChange}/></div>
    
    <div>Url: <input value={newUrl} 
                 onChange={handleUrlChange}/></div>
    <div><button type="submit">Add blog!</button></div>
    </form>
    
    
  )


return (
  
  <div>
    {loginForm}
    <h1>Blogs app</h1>
    <Notification message={errorMessage} />
    
    {user === null ?
        loginForm() :
        <div>
          <p>{user.username} logged in</p>
          {blogForm()}
          <RenderNumerot/>
          </div>

    }
  
    
    </div>
    
)
}


export default App